@charset "UTF-8";
/*ГЛОБАЛЬНЫЕ СТИЛИ*/
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans-light.woff2") format("woff2"), url("../font/opensans-light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans-regular.woff2") format("woff2"), url("../font/opensans-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans-bold.woff2") format("woff2"), url("../fonts/opensans-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

html {
  height: 100%; }

body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 320px;
  min-height: 100%;
  padding: 0;
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

/*БЛОКИ*/
.page-no-scroll {
  height: 100vh;
  overflow: hidden; }

.page-header {
  min-height: 853px;
  background-color: #283645;
  background-image: url("../img/header-back-index-desktop.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%; }
  @media (min-resolution: 2dppx) {
    .page-header {
      background-image: url("../img/header-back-index-desktop@2x.jpg");
      background-size: 100% auto; } }
  @media (max-width: 1023px) {
    .page-header {
      min-height: 729px;
      background-image: url("../img/header-back-index-tablet.jpg"); } }
  @media (max-width: 1023px) and (min-resolution: 2dppx) {
    .page-header {
      background-image: url("../img/header-back-index-tablet@2x.jpg"); } }
  @media (max-width: 659px) {
    .page-header {
      min-height: 462px;
      background-image: url("../img/header-back-index-mobile.jpg"); } }
  @media (max-width: 659px) and (min-resolution: 2dppx) {
    .page-header {
      background-image: url("../img/header-back-index-mobile@2x.jpg"); } }

@media (max-width: 1023px) {
  .page-header--nojs {
    min-height: 984px;
    background-position: center 300px; } }

@media (max-width: 659px) {
  .page-header--nojs {
    min-height: 721px;
    background-position: center 280px; } }

.page-header__navigation {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 27px 130px 43px 130px;
  box-sizing: border-box;
  background-color: rgba(0, 0, 0, 0.3); }
  @media (max-width: 1023px) {
    .page-header__navigation {
      display: block;
      padding: 0; } }

@media (max-width: 1023px) {
  .page-header--opened .page-header__navigation {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-color: #283645; } }

@media (max-width: 1023px) {
  .page-header__top-menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 27px 20px 43px 20px;
    box-sizing: border-box; } }

@media (max-width: 659px) {
  .page-header__top-menu {
    padding: 25px 20px 21px 20px; } }

@media (max-width: 1023px) {
  .page-header--nojs .page-header__top-menu,
  .page-header--opened .page-header__top-menu {
    background-color: #283645;
    box-shadow: 0px 1px 0px #000000; } }

.page-header__logo-link svg:nth-child(3n-2) {
  display: none;
  width: 74px;
  height: 23px; }
  @media (max-width: 659px) {
    .page-header__logo-link svg:nth-child(3n-2) {
      display: block; } }

.page-header__logo-link svg:nth-child(3n-1) {
  display: none;
  width: 145px;
  height: 40px; }
  @media (max-width: 1023px) {
    .page-header__logo-link svg:nth-child(3n-1) {
      display: block; } }
  @media (max-width: 659px) {
    .page-header__logo-link svg:nth-child(3n-1) {
      display: none; } }

.page-header__logo-link svg:nth-child(3n) {
  width: 147px;
  height: 40px; }
  @media (max-width: 1023px) {
    .page-header__logo-link svg:nth-child(3n) {
      display: none; } }

.page-header__button {
  display: none; }
  @media (max-width: 1023px) {
    .page-header__button {
      display: block; } }

@media (max-width: 1023px) {
  .page-header--closed .page-header__button {
    position: relative;
    width: 50px;
    height: 24px;
    margin-top: 13px;
    padding: 0;
    background-color: transparent;
    border: none; } }

@media (max-width: 659px) {
  .page-header--closed .page-header__button {
    margin-top: 0; } }

@media (max-width: 1023px) {
  .page-header--closed .page-header__button::after {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    content: "";
    width: 50px;
    height: 4px;
    background-color: #ffffff;
    border-radius: 5px;
    box-shadow: 0 10px 0 0 #ffffff, 0 -10px 0 0 #ffffff; } }

@media (max-width: 1023px) {
  .page-header--opened .page-header__button {
    position: relative;
    width: 23px;
    height: 23px;
    margin-top: 16px;
    padding: 0;
    background-color: transparent;
    border: none; } }

@media (max-width: 659px) {
  .page-header--opened .page-header__button {
    margin-top: 0; } }

@media (max-width: 1023px) {
  .page-header--opened .page-header__button::after {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(45deg);
    content: "";
    width: 33px;
    height: 4px;
    background-color: #ffffff; } }

@media (max-width: 1023px) {
  .page-header--opened .page-header__button::before {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(-45deg);
    content: "";
    width: 33px;
    height: 4px;
    background-color: #ffffff; } }

@media (max-width: 1023px) {
  .page-header--nojs .page-header__button {
    display: none; } }

.page-header__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
  width: 74.3%;
  padding-top: 18px; }
  @media (max-width: 1023px) {
    .page-header__list {
      flex-direction: column;
      width: 100%;
      padding-top: 0; } }

@media (max-width: 1023px) {
  .page-header--closed .page-header__list {
    display: none; } }

@media (max-width: 1023px) {
  .page-header--opened .page-header__list {
    display: block;
    width: 100%;
    height: calc(100vh - 109px);
    overflow-x: hidden;
    overflow-y: auto; } }

@media (max-width: 659px) {
  .page-header--opened .page-header__list {
    height: calc(100vh - 69px); } }

@media (max-width: 1023px) {
  .page-header--nojs .page-header__list {
    display: block; } }

.page-header__item {
  margin-left: 11.3%; }
  @media (max-width: 1023px) {
    .page-header__item {
      width: 100%;
      padding: 15px 15px 17px 15px;
      margin-left: 0;
      box-sizing: border-box;
      text-align: center;
      background-color: #283645;
      box-shadow: 0 1px 0 #000000, inset 0 1px 0 rgba(255, 255, 255, 0.15); } }

.page-header__link {
  font-weight: normal;
  font-size: 18px;
  line-height: 18px;
  color: #ffffff; }
  @media (max-width: 1023px) {
    .page-header__link {
      line-height: 32px;
      text-transform: uppercase; } }
